import React from "react";
import ListItem from "./ListItem";
import styles from "./List.module.scss";


const ListWrapper = ({items}) => (
    <>
    {items.length ? (
        <ul className={styles.wrapper}>
        {items.map(item => (
                <ListItem key={item.title} {...item} /> // image, name, description, link
        ))}
        </ul>
    ):(
    <h1 className={styles.noItems}>There's nothing here yet, please add some items! <span role="img" aria-label="heart">&#10084;&#65039;</span></h1>
    )}
    </>
);

export default ListWrapper;
